interface NumberArray {
  [index: number]: number;
}

const array: NumberArray = [1, 2, 3, 4]

// arrayLike object

function sum (x: number, y: number): number {
  return x + y
}
const list: unknown[] = ['xcatliu', 25, { website: 'http://xcatliu.com' }]

const mySum: (x: number, y: number) => number = function (x, y) {
  return x + y
}

interface SearchFunc {
  (source: string, subString: string): boolean;
}

const mySearch: SearchFunc = function (source: string, subString: string) {
  return source.search(subString) !== -1
}

function buildName (firstName = 'Tom', lastName: string, ...items: unknown[]) {
  return firstName + ' ' + lastName + items[0]
}
const tomcat = buildName('Tom', 'Cat', 'hello')
const cat = buildName(undefined, 'Cat', 111)

function reverse(x: number): number;
function reverse(x: string): string;
function reverse (x: number | string): number | string {
  if (typeof x === 'number') {
    return Number(x.toString().split('').reverse().join(''))
  } else if (typeof x === 'string') {
    return x.split('').reverse().join('')
  } else {
    return 0
  }
}

