interface ApiError extends Error {
  code: number;
}
interface HttpError extends Error {
  statusCode: number;
}

function isApiError (error: Error) {
  if (typeof (error as ApiError).code === 'number') {
    return true
  }
  return false
}

const api: ApiError = {
  code: 10000,
  name: 'apiError',
  message: 'error happens'
}
console.log(isApiError(api))

interface Animal {
  name: string;
}
interface Cat {
  name: string;
  run(): void;
}

function testAnimal (animal: Animal) {
  return (animal as Cat)
}
function testCat (cat: Cat) {
  return (cat as Animal)
}

const dog: Animal = {
  name: 'dogge'
}
const catty: Cat = {
  name: 'catty',
  run () { console.log('cat run') }
}

export function testTypeScript (str1: string, obj1: object, ...args: number[]): void {
  const unusable = null
  sum(1, 2)
  mySum(1, 2)
  console.log(str1, obj1, ...args, unusable, array, list, mySearch('aaa', 'a'), tomcat, cat)
  console.log(sayHello(undefined, 233))
  console.log(reverse(123), reverse('abc'))
  console.log(testAnimal(dog), testCat(catty))
}
/*
  联合类型可以被断言为其中一个类型
  父类可以被断言为子类
  任何类型都可以被断言为 any
  any 可以被断言为任何类型
  要使得 A 能够被断言为 B，只需要 A 兼容 B 或 B 兼容 A 即可
*/

interface Animal {
  name: string;
}
interface Cat {
  name: string;
  run(): void;
}

const animal: Cat = {
  name: 'tom',
  run () { console.log(11) }
}
const taaa = animal as Cat

console.log(taaa)
